.c-popup {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background-color: rgba(0,0,0,0.425);
  display: flex;
  &__content {
    position: relative;
    transform: translateY(-1000px);
    @include tr(all 500ms ease-in-out);
    position: relative;
    margin: auto;
    max-width: 800px;
    padding: 15px 20px;
    width: 96%;
    background-color: #f5f5f5;
    border-radius: 4px;
    border: 1px solid #ccc;
    &.is-active {
      transform: translateY(0px); } }
  &__controll {
    position: absolute;
    display: block;
    background-color: transparent;
    z-index: 100;
    &_prev {
      left: 2%;
      @include arr(19,30,yellow,l);
      @include center(vertical); }
    &_next {
      right: 2%;
      @include arr(19,30,yellow,r);
      @include center(vertical); } } }


.popup-slider {
  position: relative;
  overflow: visible;

  &__list {
    position: relative;
    min-height: 600px; }
  &__item {
    width: 100%;
    min-height: 600px;
    @include vcenter;
    display: flex;
    opacity: 0;
    z-index: 101;
    // +tr(opacity 500ms ease-in)
    &.is-active {
      opacity: 1;
      z-index: 200; } }
  &__close {
    width: 25px;
    height: 25px;
    position: absolute;
    z-index: 201;
    top: -40px;
    right: -40px; } }


.close-btn {
  font-size: 30px;
  background: none;
  color: #ffffff;
  font-weight: 300; }

.popup-slide-item {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  &__left {
    flex: 0 0 45%;
    padding: 10px; }
  &__right {
    flex: 0 0 400px;
    padding: 14px; }
  &__img {
    min-height: 600px;
    @include cover; }
  &__heading {
    font-size: 18px;
    margin-bottom: 15px; }
  &__descr {
    margin-bottom: 15px;
    font-size: 16px;
    color: #050505;
    p {
      margin-bottom: 15px; } }
  &__link {
    color: inherit;
    text-decoration: none;
    color: #151515;
    border-bottom: 0.5px solid #151515; }
  &__count-box {
    position: absolute;
    right: 14px;
    bottom: 14px;
    background-color: #050500;
    padding: 2px 5px;
    color: #ffffff; } }
