html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.c-popup {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.425);
  display: flex;
}

.c-popup__content {
  position: relative;
  transform: translateY(-1000px);
  transition: all 500ms ease-in-out;
  position: relative;
  margin: auto;
  max-width: 800px;
  padding: 15px 20px;
  width: 96%;
  background-color: #f5f5f5;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.c-popup__content.is-active {
  transform: translateY(0px);
}

.c-popup__controll {
  position: absolute;
  display: block;
  background-color: transparent;
  z-index: 100;
}

.c-popup__controll_prev {
  left: 2%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 15px 19px 15px 0;
  border-color: transparent yellow transparent transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.c-popup__controll_next {
  right: 2%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 15px 0 15px 19px;
  border-color: transparent transparent transparent yellow;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.popup-slider {
  position: relative;
  overflow: visible;
}

.popup-slider__list {
  position: relative;
  min-height: 600px;
}

.popup-slider__item {
  width: 100%;
  min-height: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  opacity: 0;
  z-index: 101;
}

.popup-slider__item.is-active {
  opacity: 1;
  z-index: 200;
}

.popup-slider__close {
  width: 25px;
  height: 25px;
  position: absolute;
  z-index: 201;
  top: -40px;
  right: -40px;
}

.close-btn {
  font-size: 30px;
  background: none;
  color: #ffffff;
  font-weight: 300;
}

.popup-slide-item {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.popup-slide-item__left {
  flex: 0 0 45%;
  padding: 10px;
}

.popup-slide-item__right {
  flex: 0 0 400px;
  padding: 14px;
}

.popup-slide-item__img {
  min-height: 600px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.popup-slide-item__heading {
  font-size: 18px;
  margin-bottom: 15px;
}

.popup-slide-item__descr {
  margin-bottom: 15px;
  font-size: 16px;
  color: #050505;
}

.popup-slide-item__descr p {
  margin-bottom: 15px;
}

.popup-slide-item__link {
  color: inherit;
  text-decoration: none;
  color: #151515;
  border-bottom: 0.5px solid #151515;
}

.popup-slide-item__count-box {
  position: absolute;
  right: 14px;
  bottom: 14px;
  background-color: #050500;
  padding: 2px 5px;
  color: #ffffff;
}

body {
  position: relative;
  background-color: #929ef5;
  min-height: 100vh;
  overflow: hidden;
}

.header {
  min-height: 60px;
  background-color: #325be9;
  color: #ffffff;
  box-shadow: 0px 0px 0px 2px #ddd;
}

.header__title {
  text-align: center;
  vertical-align: middle;
  font-weight: 500;
  font-size: 34px;
  line-height: 60px;
  font-family: monospace;
}

.tabs {
  margin: 25px auto;
  max-width: 80%;
  text-align: center;
  padding: 5px;
}

.tabs__item {
  display: inline-block;
  font-size: 16px;
  font-family: monospace;
  font-weight: 400;
  border-radius: 8px;
  background-color: #323232;
  color: #fefefe;
  border: 1px solid #ddd;
}

.tabs__link {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  padding: 10px 16px;
  border-radius: 8px;
}

.tabs__link.is-active {
  background-color: #325be9;
}

.heroes__item {
  position: relative;
  min-height: 600px;
  max-width: 620px;
  margin: 25px auto;
}

.c-slider {
  display: none;
  min-height: 600px;
  border-radius: 3px;
  font-family: monospace;
  position: relative;
  padding-top: 25px;
}

.c-slider.is-active {
  display: flex;
}

.c-slider_kichot {
  background-color: #fefeaa;
}

.c-slider_zatoichi {
  background-color: #aafefe;
}

.c-slider__wrap {
  overflow: hidden;
  position: relative;
}

.c-slider__list {
  transition: all 0.4s ease-in-out;
  transform: translateX(0px);
  margin: auto;
  overflow: hidden;
}

.c-slider__item {
  display: inline-block;
  width: 250px;
  margin: 0px 30px;
}

.c-slider .c-slide {
  position: relative;
  width: 100%;
  min-height: 500px;
  background-color: #ffffff;
  color: #333333;
  padding-bottom: 40px;
}

.c-slider .c-slide__inner {
  height: 100%;
  padding: 10px 15px;
}

.c-slider .c-slide__heading {
  font-size: 18px;
  margin-bottom: 10px;
}

.c-slider .c-slide__descr {
  font-size: 14px;
  margin-bottom: 10px;
}

.c-slider .c-slide__date {
  font-size: 12px;
  font-weight: 600;
}

.c-slider .c-slide__img {
  width: 100%;
  height: 250px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.c-slider .c-slide__controll {
  position: absolute;
  display: block;
  background-color: transparent;
  z-index: 100;
}

.c-slider .c-slide__controll_prev {
  left: -40px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 15px 19px 15px 0;
  border-color: transparent #325be9 transparent transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.c-slider .c-slide__controll_next {
  right: -40px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 15px 0 15px 19px;
  border-color: transparent transparent transparent #325be9;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.c-slider .c-slide__footer {
  position: absolute;
  width: 100%;
  bottom: 0;
}

.show-more-btn {
  display: block;
  height: 40px;
  width: 100%;
  line-height: 40px;
  font-size: 20px;
  text-transform: uppercase;
  vertical-align: middle;
  text-align: center;
  color: #ffffff;
  background-color: #4CBFF0;
}

.animate {
  transition: all .5s ease-out;
}
